import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'tagIdName'
})
export class TagIdNamePipe implements PipeTransform {

  transform(value, args) {
    if(!args && isEmpty(args)){
      return;
    }

      let output = '';
      return args[value];
    }

}

export function isEmpty(obj) {
  for(var key in obj) {
       if(obj.hasOwnProperty(key))
           return false;
   }
   return true;
}



