import { Component, OnInit, Input } from '@angular/core';
import { WpServiceService } from '../wp-service/wp-service.service';
import { TagIdNamePipe } from '../tag-id-name.pipe';
import { trigger, style, transition, animate, keyframes, query, stagger } from '@angular/animations';


@Component({
  selector: 'grid',
  templateUrl: './grid.component.html',
  styleUrls: ['./grid.component.scss']
})
export class GridComponent implements OnInit {

  @Input('catId') cat;
  @Input('postCount') catCount;

  public count = 6;
  public postList;
  public paginator = true;
  private toggleClass = false;
  public isLoading = false;

  constructor(private wpService: WpServiceService) { }

  ngOnInit() {

    // Cheak pagination
    if (this.catCount < 6) {
      this.paginator = false;
    }

    // Load Post
    this.fetchPost(this.cat, this.count)

  }


  // Paginator Click
  loadPost() {
    // Cheak pagination
    if ((this.catCount - this.count) < 6) {
      this.count = this.catCount;
      this.paginator = false;
    }
    else {
      this.count = this.count + 6;
    }

    // Load Post
    this.fetchPost(this.cat, this.count)

  }



  fetchPost(cat, count) {
    this.isLoading = true;
    this.wpService.getTags().subscribe(
      res => { 
       // Fetch post data
        this.wpService.getData(cat, count).subscribe(
          res => {
            this.isLoading = false;
            this.postList = res;
          }
        )
      }
    )
  }
}





