import { Component, OnInit , Input } from '@angular/core';
import { WpServiceService} from '../wp-service/wp-service.service';
import { TagIdNamePipe } from '../tag-id-name.pipe';
import { trigger, style , transition , animate , keyframes , query , stagger } from '@angular/animations';


@Component({
  selector: 'card',
  templateUrl: './card.component.html',
  styleUrls: ['./card.component.scss'],
  providers: [WpServiceService],
})
export class CardComponent implements OnInit {

  @Input('postData') post;
  public tags;
  public tagArray = {};
  private toggleClass = false;

  constructor( private wpService : WpServiceService ) { }

  ngOnInit() {
    // Tag name Finder
    this.wpService.getTags().subscribe(
      res => {
        res.forEach((item) => {
          this.tagArray[item.id] = item.name;
        })
      }
    )
  }

  // Card open
  toggleCard() {
    this.toggleClass = !this.toggleClass;

    document.getElementsByTagName("html")[0].classList.toggle('oh')
  }
}





